import Home from '../components/Home'
// import Appmain from '../components/Appmain'
// import Appchat from '../components/Appchat'
// import Requests from '../components/AppContents/Requests'


const Appmain = resolve => {
    require.ensure(['../components/Appmain'],()=>{
        resolve(require('../components/Appmain'))
    });
};

const Appchat = resolve => {
    require.ensure(['../components/Appchat'],()=>{
        resolve(require('../components/Appchat'))
    });
};

const Requests = resolve => {
    require.ensure(['../components/AppContents/Requests'],()=>{
        resolve(require('../components/AppContents/Requests'))
    });
};

export const routes = [
    {
        path: '',
        component: Home
        
    },
    {
        path: '/rhymeapplobby',
        component: Appmain,
        meta:{
            requiresAuth:true
        }
    },
    {
        path: '/rhymeappchat/:con_id',
        component: Appchat,
        meta:{
            requiresAuth:true
        }
    },
    {
        path: '/rhymeapprequests',
        component: Requests,
        meta:{
            requiresAuth:true
        }
        
    }
];