import Vue from 'vue'
import App from './App.vue'
import firebase from 'firebase'
import 'firebase/app';
import 'firebase/firestore';

import VueRouter from 'vue-router'
import {routes} from './router/router'


//constfireplaceholder
const fireconf =  {
  apiKey: "AIzaSyAPPDsB1XguiEif3oHc4arUo-LrckXLIEc",
  authDomain: "rhyme-chat.firebaseapp.com",
  databaseURL: "https://rhyme-chat.firebaseio.com",
  projectId: "rhyme-chat",
  storageBucket: "rhyme-chat.appspot.com",
  messagingSenderId: "106826970333",
  appId: "1:106826970333:web:a397601915d9258ee71734",
  measurementId: "G-KPPW2VC85H"
};

Vue.config.productionTip = false;


Vue.use(VueRouter);
let router = new VueRouter({
  routes
});

//routing-protec
 router.beforeEach((to,from,next)=>{
  const requiresAuth = to.matched.some(record=> record.meta.requiresAuth);
  const isAuthenticated = firebase.auth().currentUser;
  if(requiresAuth && !isAuthenticated){
    next({path:'/'});
  }
  else{
    next();
  }
})

firebase.initializeApp(fireconf);
console.clear();

let app;
firebase.auth().onAuthStateChanged(()=>{
  
  if(!app){
    app= new Vue({
      router,
      render: h => h(App)
      
    }).$mount('#app')
  }
})


