<template>
<div>
    <div class="outer" v-if="signin_status">   
        <div class="sign-main" >
            <form @submit.prevent="signInAuth" name="signInForm">
                <label for="email">Email:</label><br>
                <input type="email" name="email" class="sign-inp" v-model="in_email" autocomplete="off"><br>
                <label for="pass">Password:</label><br>
                <input type="password" name="pass" class="sign-inp" v-model="in_pass"><br>
                <input type="submit" value="Signin" class="sign-in-but">
            </form>
            <div class="err_mess">{{err_mess2}}</div>
        </div>
    </div>  
    <div class="outer" v-if="signup_status">
        <div class="sign-main" >
            <form @submit.prevent="signUpAuth" name="signUpForm">
                <label for="email">Email:</label><br>
                <input type="email" name="email" class="sign-inp" v-model="email" autocomplete="off"><br>
                <label for="Nametag" >Nametag:</label><br>
                <input type="text" name="nametag" class="sign-inp" v-model="nametag" autocomplete="off"><br>
                <label for="pass">Password:</label><br>
                <input type="password" name="pass" class="sign-inp" v-model="pass"><br>
                <label for="cpass">Confirm password:</label><br>
                <input type="password" name="cpass" class="sign-inp" v-model="conf_pass"><br>
                <input type="submit" value="Signup" class="sign-in-but">
                
            </form>
            <div class="err_mess">{{err_mess}}</div>
        </div>
    </div>   

</div>
</template>

<script>
import * as firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore'

export default {
    name:'Sign',
    props:["signin_status","signup_status"],
    data(){
        return{
            in_email:'',
            in_pass:'',
            email:'',
            nametag:'',
            pass:'',
            conf_pass:'',
            err_mess:'',
            err_mess2:'',
            okreg:false,
            oksign:false,
            falser:false,
            tags_avail:[]

        }
    },
    created(){
    let fs = firebase.firestore();
    fs.collection("tags").doc("xx_rhyme_tags_xx").onSnapshot((doc)=>{
       if(doc.exists){
         this.tags_avail=doc.data().xx_names_xx;
       }else{
         console.log('failed to parse')
       }
     })
    
     },
    methods:{
        async signInAuth(){
            
            if(this.in_email == '' || this.in_pass==''){
                this.err_mess2='* Fields empty'
            }
            else{
                
                 await firebase.auth().signInWithEmailAndPassword(this.in_email,this.in_pass).then(()=>{
                          this.in_email=''
                           this.in_pass=''
                           this.$router.push({path:'/rhymeapplobby'})
                      },
                      err=>{
                          console.log(err);
                        this.err_mess2='* Login failed';
                      }); 
                
            }
    

        },
        async signUpAuth(){
            const firedb = firebase.firestore();
            console.log({email:this.email,nametag:this.nametag,pass:this.pass,conf_pass:this.conf_pass })
            if(this.email == '' || this.nametag=='' || this.pass==''||this.conf_pass==''){
                this.err_mess='* Fields empty'
            }
            else if(! this.tags_avail.includes(this.nametag)){
                if(this.pass == this.conf_pass){
                    if(this.pass.length > 5){
                      await firebase.auth().createUserWithEmailAndPassword(this.email,this.pass).then((user)=>{                  
                           firedb.collection("users").doc(user.user.uid).set({
                                r_email: this.email,
                                r_nametag: this.nametag,
                                r_friends:[],
                                r_pendingReqs:[],
                                r_convoThreads:[]
                                 })
                                .then(()=> {
                                     console.log("Added user to fs");
                                     this.$emit("signupfalser",this.falser);
                                })
                                .catch(function(error) {
                                    console.error("Error adding document: ", error);
                                    });
                          this.email=''
                           this.nametag=''
                           this.pass=''
                           this.conf_pass=''
                           this.err_mess=''
                      },
                      err=>{
                          console.log(err);
                        this.err_mess='* Email in use/invalid';
                      }); 
                      
                    }else{
                        this.err_mess='* Password weak';
                    }
            }
                else{
                    this.err_mess='* Passwords donot match';
                }
                }else{
                    this.err_mess='* Nametag already taken'
                }
            }
        }
    }

</script>

<style scoped>
.outer{
    width: 100%;
    position: absolute;
    /* background-color: white; */
    
    display: flex;
    flex-direction:column;
    align-items:flex-end;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}
.sign-main{

    padding: 1rem;
    background-color: rgb(31, 28, 46);
    border:1px solid rgb(66, 58, 117);
    color: white;
    font-family: 'Poppins',sans-serif;
    text-shadow:1px 1px blueviolet;
    border-radius:0 0 0 0.3rem ;
}

.sign-inp{
    height:1.5rem;
    width: 10rem;
    background-color: rgb(33, 30, 48);
    border:1px solid rgb(78, 69, 134);
    outline: 0;
    border-radius: 0.1rem;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
    color: white;
    font-family: 'Poppins',sans-serif;
}
.sign-in-but{
    background-color: rgb(33, 30, 48);
    border: 1px solid rgb(78, 69, 134);
    color: white;
    font-family: 'Poppins',sans-serif;
    border-radius: 0.1rem;
    margin-top: 0.5rem;
    height: 2rem;
    width: 40%;
    outline: 0;
}
.sign-in-but:hover{
   background-color: rgb(39, 35, 56); 
   
}
.err_mess{
    margin-top: 0.5rem;
    text-align: center;
    font-size: 0.7rem;
    color: rgb(255, 116, 116);
    text-shadow: 1px 1px rgb(132, 1, 255);
    
}
</style>